import React ,{Component} from 'react';
import {Link } from "react-router-dom";
import F2 from '@antv/f2';
import Tooltip  from '@antv/f2/lib/plugin/tooltip';
import './yearDetail.less';
import PropTypes  from "prop-types";
import { connect } from "react-redux";

F2.Chart.plugins.register(Tooltip);
class YearDetail extends Component {
    constructor(props){
        super(props);
        this.state = {
            width: 100,
            height: 100,
        }
    }
    // 用于计算cover的宽高
    getConverWidthAndHeight(){
        var docEl = document.documentElement
        let maxWidth = 750
        let cw = docEl.clientWidth>maxWidth ? maxWidth : docEl.clientWidth
        var fontSize =16 * (cw / 320);
        const width = fontSize *  16.375
        const height = fontSize * 10.5
        return {width: width , height: height} 
      }
    componentDidMount(){
          let getData =  ""
        // 柱状图
          var showIntervalData = [
              {
                  year: '1月',
                  sales: 38
                }, {
                  year: '2月',
                  sales: 52
                }, {
                  year: '3月',
                  sales: 61
                }, {
                  year: '4月',
                  sales: 145
                }, {
                  year: '5月',
                  sales: 48
                }, {
                  year: '6月',
                  sales: 38
                }, {
                  year: '7月',
                  sales: 38
                }, {
                  year: '8月',
                  sales: 38
                }, {
                  year: '9月',
                  sales: 48
                }, {
                  year: '10月',
                  sales: 38
                }, {
                  year: '11月',
                  sales: 38
                }, {
                  year: '12月',
                  sales: 38
                }
            ];
              // 折线图
          var showLinerData = [
                  {
                    year: '1月',
                    sales: 38
                  }, {
                    year: '2月',
                    sales: 52
                  }, {
                    year: '3月',
                    sales: 61
                  }, {
                    year: '4月',
                    sales: 145
                  }, {
                    year: '5月',
                    sales: 48
                  }, {
                    year: '6月',
                    sales: 38
                  }, {
                    year: '7月',
                    sales: 38
                  }, {
                    year: '8月',
                    sales: 38
                  }, {
                    year: '9月',
                    sales: 48
                  }, {
                    year: '10月',
                    sales: 38
                  }, {
                    year: '11月',
                    sales: 38
                  }, {
                    year: '12月',
                    sales: 38
                  }
              ];
          var data = window.localStorage.getItem('projectList')
          data = JSON.parse(data)
          if(Object.keys(this.props.posts).length === 0 && data){
                  getData = data
          }else{
              getData = this.props.posts.data
          
          }
          if(getData && Object.keys(getData).length !== 0){
             var  i = 0 ;
             var  j = 0;
             let ElectricityConsumption = getData.yearElectricityConsumption || {}
             let dataOrgNo = getData.dataOrgNo || {}
             for(var key in ElectricityConsumption){
              showIntervalData[i].sales = ElectricityConsumption[key]
              i++
             }
             for(var key in dataOrgNo){
              showLinerData[j].sales = dataOrgNo[key]
              j++
             }
          }
      this.showInterval(showIntervalData)
      this.showLiner(showLinerData)
    }
    showInterval(data){
      var chart = new F2.Chart({
        id: 'myChart',
        pixelRatio: window.devicePixelRatio
      });
      
      chart.source(data, {
        sales: {
          tickCount: 5
        }
      });
      chart.tooltip({
        showItemMarker: false,
        onShow: function onShow(ev) {
          var items = ev.items;
          items[0].name = null;
          items[0].name = items[0].title;
          items[0].value = '¥ ' + items[0].value;
        }
      });
      chart.interval().position('year*sales');
      chart.render();
    }
    showLiner(data1){
      var chart1 = new F2.Chart({
        id: 'mountNode',
        pixelRatio: window.devicePixelRatio
      });
      
      chart1.source(data1, {
        value: {
          tickCount: 5,
          min: 0
        },
        date: {
          type: 'timeCat',
          range: [0, 1],
          tickCount: 3
        }
      });
      chart1.tooltip({
        custom: true,
        showXTip: true,
        showYTip: true,
        snap: true,
        crosshairsType: 'xy',
        crosshairsStyle: {
          lineDash: [2]
        }
      });
      chart1.axis('sales',{
        grid:null,
        position: 'right'
      });
      chart1.axis('year',false)
      chart1.legend({
        position: 'bottom',
        custom: true,
        itemWidth: '3rem',
        items: [{
          name: '用电量',
          marker:function marker(x, y, r, ctx) {
            ctx.lineWidth = 1;
            ctx.strokeStyle = ctx.fillStyle;
            ctx.moveTo(x - r - 3, y);
            ctx.lineTo(x + r + 3, y);
            ctx.stroke();
            ctx.fill();
          },
          fill: '#3b63b7'
         
        }, {
          name: '平均气温',
          marker: function marker(x, y, r, ctx) {
            ctx.lineWidth = 1;
            ctx.strokeStyle = ctx.fillStyle;
            ctx.moveTo(x - r - 3, y);
            ctx.lineTo(x + r + 3, y);
            ctx.stroke();
            ctx.fill();
          },
          fill: '#e65e0c'
         
        }],
       
      });
      chart1.line().position('year*sales').color('#e34409');
      chart1.render();
    }
    render(){
       var {width ,height} =  this.getConverWidthAndHeight()
        return (
            <div className="yearDetail">
                 <Link to="/yearHot">
                 </Link>
                 <canvas id="myChart"  style={{width: width,height:height}}></canvas>
                 <canvas id="mountNode"  style={{width: width,height:height}}></canvas>
                 <div className="length"></div>
            </div>
        )
    }
}
YearDetail.PropTypes = {
  posts: PropTypes.array.isRequired
}
const mapStateToProps = state =>({
  posts: state.posts.items
})
export default connect(mapStateToProps,{})(YearDetail)
