import React ,{Component} from 'react';
import {Link } from "react-router-dom";
import './yearHot.less'
import PropTypes  from "prop-types";
import { connect } from "react-redux";
class YearHot extends Component {
    constructor(props){
        super(props)
        this.state ={
            MAX_PQ_DATE:"",
            MAX_PQ:"",
            M_WEATHER:"",
            M_TEMP:"",
            M_CONS_COUNT:"",
            toUrl: "/girlPay"

        }
    }
    timeFormat(timestamp){
        function add0(m){return m<10?'0'+m:m }
        //timestamp是整数，否则要parseInt转换,不会出现少个0的情况
          var time = new Date(timestamp);
          var year = time.getFullYear();
          var month = time.getMonth()+1;
          var date = time.getDate();
          var hours = time.getHours();
          var minutes = time.getMinutes();
          var seconds = time.getSeconds();
          return year+'年'+add0(month)+'月'+add0(date)+'日'
        //   +' '+add0(hours)+':'+add0(minutes)+':'+add0(seconds);
    }
    componentDidMount(){
        let getData =  ""
        let year = ""
        let IS_LADDER_INFO = ""
        var data = window.localStorage.getItem('projectList')
        data = JSON.parse(data)
        if(Object.keys(this.props.posts).length === 0 && data){
                getData = data
        }else{
            getData = this.props.posts.data
        
        }
        if(getData && Object.keys(getData).length !== 0){
            year = getData.annualBill.maxPqDate ? this.timeFormat(getData.annualBill.maxPqDate): "";
            IS_LADDER_INFO = getData.annualBill.isLadderInfo
            this.setState({
                MAX_PQ_DATE: year,
                MAX_PQ: getData.annualBill.maxPq|| "",
                M_WEATHER: getData.annualBill.maxDayWeather ||"",
                M_TEMP:getData.annualBill.maxDayTempture ||"",
                M_CONS_COUNT:getData.annualBill.mConsCount || "",
                toUrl: getData.annualBill.isLadderInfo ? "/ladder" : '/girlPay'
            })
            
        }
    }
    render(){
        return (
            <div className="yearHot">
                <Link to={this.state.toUrl}>
                    <div className="content">
                        <h1> 全年最高用电日</h1>
                        <p>{ this.state.MAX_PQ_DATE }</p>
                        <p>温度{ this.state.M_TEMP }℃</p>
                        <p>当日用电量{ this.state.MAX_PQ }度</p>
                        <p style={{ fontSize: "16px" }}>本区域{ this.state.M_CONS_COUNT }户，当日用电量最大</p>
                    </div>
                </Link>
            </div>
        )
    }
}
YearHot.PropTypes = {
    posts: PropTypes.array.isRequired
}
const mapStateToProps = state =>({
    posts: state.posts.items
})
export default connect(mapStateToProps,{})(YearHot)