import React, { Component } from 'react';
import { Link } from "react-router-dom";
import './AppIndex.less';
import PropTypes  from "prop-types";
import { connect } from "react-redux";
import { onclick } from "../../actions/onClickActions";

class AppIndex extends Component {
    constructor(props){
      super(props)
      this.state ={
        consNo: ""
      }
    }
    linkOnclick(){
      console.log('onclick---test')
    }
    showWindowHref(){
      var href = window.location.href
      var args = href.split('?')
      if(args[0] === href){
      return " "
      }
      var args2 = args[1].split('&')
      var obj = {}
      for(var i = 0 ; i < args2.length;i++){
      var list = args2[i].split('=')
      obj[list[0]] =list[1]
      }
        return obj
    }   
    componentDidMount(){
      // 获取入参
      const obj = this.showWindowHref()
      let consno = obj.consno || null
      let orgno = obj.orgno|| null
      let channel = obj.channel|| null
      // console.log(consno,orgno,channel)
      // 触发action 动作。
      this.props.onclick(consno,orgno,channel);
    }

    // redux 改变数据后走的生命周期
    componentWillReceiveProps(nextProps){
      this.screen(nextProps.posts)
    }
    // 整理数据 
    screen(data){
      let consNo = ""
      data = data.data
      // 拿到数据。   // 展示存储存在缓存里面。以免后面组件刷新的时候为空
      const projectList = JSON.stringify(data)
      window.localStorage.setItem('projectList',projectList)
      // 填充数据
      if(data && Object.keys(data) !== 0){
         consNo = data.annualBill ? data.annualBill.consNo : ""
      }
      this.setState({
        consNo: consNo
      })

    }
    render() {
      return (
        <div className="AppIndex">
        <Link to="/Welcome" onClick={this.linkOnclick.bind(this)}> 
           <div className="content" >
                <h2>用电户号:</h2>
                <h2>{ this.state.consNo }</h2>
           </div> 
         </Link>
       </div>
      );
    }
  }
AppIndex.PropTypes = {
    onclick: PropTypes.func.isRequired,
    posts: PropTypes.array.isRequired
}
const mapStateToProps = state =>({
    posts: state.posts.items
})
export default connect(mapStateToProps,{ onclick })(AppIndex)