import React , { Component }  from 'react';
import {  Link } from "react-router-dom";
import './welcome.less';
import PropTypes  from "prop-types";
import { connect } from "react-redux";

class Welcome extends Component{
    constructor(props){
        super(props)
        this.state = {
            year: "X年X月X日",
            days: 0
        }
    }
    timeFormat(timestamp){
        function add0(m){return m<10?'0'+m:m }
        //timestamp是整数，否则要parseInt转换,不会出现少个0的情况
          var time = new Date(timestamp);
          var year = time.getFullYear();
          var month = time.getMonth()+1;
          var date = time.getDate();
          var hours = time.getHours();
          var minutes = time.getMinutes();
          var seconds = time.getSeconds();
          return year+'年'+add0(month)+'月'+add0(date)+'日'
        //   +' '+add0(hours)+':'+add0(minutes)+':'+add0(seconds);
    }
    componentDidMount(){
        let getData =  ""
        let year = ""
        let days = 0
        var data = window.localStorage.getItem('projectList')
        data = JSON.parse(data)
        if(Object.keys(this.props.posts).length === 0 && data){
                getData = data
        }else{
            getData = this.props.posts.data
        
        }
        if(getData && Object.keys(getData).length !== 0){
            year =  getData.annualBill ? getData.annualBill.buildDate : ""
            days =  getData.fewDays ? getData.fewDays : 0
            if(year && year > 0){
                year = this.timeFormat(year)
            }else{
                year = ""
            }
            this.setState({
                year: year,
                days: days
            })
        }
    }
    render(){
        return(
            <div className="Welcome">
            <Link to="/showAll">
                <div className="content">
                   <p> { this.state.year }，接入电网 一起相伴走过 { this.state.days }天</p>
                </div>
            </Link>
            </div>
        )
    }
}
Welcome.PropTypes = {
    posts: PropTypes.array.isRequired
}
const mapStateToProps = state =>({
    posts: state.posts.items
})
export default connect(mapStateToProps,{})(Welcome)