import React , { Component }  from 'react';
import F2 from '@antv/f2';
import Tooltip  from '@antv/f2/lib/plugin/tooltip';
import {Link } from "react-router-dom";
import './showall.less'
import PropTypes  from "prop-types";
import { connect } from "react-redux";

F2.Chart.plugins.register(Tooltip);
class ShowAll extends Component{
    constructor(porps){
        super(porps)
        this.state ={
            ELEC_YEAR_AMT: "",
            RANK_PRECENT:"",
            AMT_RANK:"",
            T_PQ_YEAR:"",
            T_PQ_LAST_YEAR:"",
            ELEC_YREAR_ADD:"",
            left: '50%'
        }
    }
    componentDidMount(){
        let getData =  ""
        let RANK_PRECENT = ""
        var data = window.localStorage.getItem('projectList')
        data = JSON.parse(data)
        if(Object.keys(this.props.posts).length === 0 && data){
                getData = data
        }else{
                getData = this.props.posts.data
        
        }
        if(getData && Object.keys(getData).length !== 0){
            // 判断指针的位置
           RANK_PRECENT = getData.annualBill.rankPercent
           this.setState({
            ELEC_YEAR_AMT:getData.annualBill.elecYearAmt || "",
            RANK_PRECENT: getData.annualBill.rankPercent || "", 
            AMT_RANK:  getData.annualBill.amtRank || "",
            T_PQ_YEAR: getData.annualBill.tPqYear || "",
            T_PQ_LAST_YEAR: getData.annualBill.tPqLastYear || "",
            ELEC_YREAR_ADD: getData.annualBill.elecYrearAdd || ""
           })   
        }
        if(RANK_PRECENT){
            this.PercentPosition(RANK_PRECENT)
        }
        
    }
     // 根据%计算位置
    PercentPosition(rank){
        this.setState({
            left: rank
        })
    }
    timeFormat(timestamp){
        function add0(m){return m<10?'0'+m:m }
        //timestamp是整数，否则要parseInt转换,不会出现少个0的情况
          var time = new Date(timestamp);
          var year = time.getFullYear();
          var month = time.getMonth()+1;
          var date = time.getDate();
          var hours = time.getHours();
          var minutes = time.getMinutes();
          var seconds = time.getSeconds();
          return year+'年'+add0(month)+'月'+add0(date)+'日'
        //   +' '+add0(hours)+':'+add0(minutes)+':'+add0(seconds);
    }
    rendLiner(){
        const data = [
            { genre: 'Sports', sold: 275 },
            { genre: 'Strategy', sold: 115 },
            { genre: 'Action', sold: 120 },
            { genre: 'Shooter', sold: 350 },
            { genre: 'Other', sold: 150 },
          ];
          
          // Step 1: 创建 Chart 对象
          const chart = new F2.Chart({
            id: 'myChart',
            pixelRatio: window.devicePixelRatio, // 指定分辨率
            plugins: Tooltip
          });
        
          chart.axis('genre', false)
          // Step 2: 载入数据源
          chart.source(data);
          
          // Step 3：创建图形语法，绘制柱状图，由 genre 和 sold 两个属性决定图形位置，genre 映射至 x 轴，sold 映射至 y 轴
          chart.interval().position('genre*sold');
          
          // Step 4: 渲染图表
          chart.render();
    }
    // 用于计算cover的宽高
    getConverWidthAndHeight(){
    var docEl = document.documentElement
    let maxWidth = 750
    let cw = docEl.clientWidth>maxWidth ? maxWidth : docEl.clientWidth
    var fontSize =16 * (cw / 320);
    const width = fontSize *  9
    const height = fontSize * 7
    return {width: width , height: height} 
    }
    RightClick(event){
        event.stopPropagation();
    }
    render(){
        var {width ,height} =  this.getConverWidthAndHeight()
        return(
            <div className="showAll">
               <Link to="/yearDetail">  
                    <div className="content">
                        <div className="stickyNoteLeft">
                            <p className="world_14">2018年</p>
                            <p className="world_14"> 支持电费：</p>
                            <p><span> { this.state.ELEC_YEAR_AMT }元</span></p>
                            <p>本区域排名：</p>
                            <p>{ this.state.AMT_RANK }名</p>
                            <p>超过{this.state.RANK_PRECENT}的用户</p>
                            <div className="Ruler">
                                <div className="score" style={{ left: this.state.left}}>{this.state.RANK_PRECENT}</div>
                            </div>
                        </div>
                       
                    </div>
               </Link>
               <div className="stickyNoteRigth" onClick= { this.RightClick }>
                    <p className="world_14">全年用电{this.state.T_PQ_YEAR}度</p>
                    { 
                        //<canvas id="myChart"  style={{position:'absolute',left:'1rem',top:'4.5rem',width: width,height:height}}></canvas> 
                    }
                    <div className="textli"></div>
                    <p className="world">比去年多用（少用）{this.state.ELEC_YREAR_ADD}度</p>
                </div>
            </div>
        )
    }
}
ShowAll.PropTypes = {
    posts: PropTypes.array.isRequired
}
const mapStateToProps = state =>({
    posts: state.posts.items
})
export default connect(mapStateToProps,{})(ShowAll)