import React,{ Component} from 'react'
import {Link} from 'react-router-dom'
import F2 from '@antv/f2';
import starbg from '../images/starbg.png'
import kongStar from '../images/kongStar.png'
import './star.less'
import PropTypes  from "prop-types";
import { connect } from "react-redux";

class Star extends Component{
    constructor(props){
        super(props)
        this.state={
            star: 4
        }
    }
     // 用于计算cover的宽高
     getConverWidthAndHeight(){
        var docEl = document.documentElement
        let maxWidth = 750
        let cw = docEl.clientWidth>maxWidth ? maxWidth : docEl.clientWidth
        var fontSize =16 * (cw / 320);
        const width = fontSize * 17.1875
        const height = fontSize * 8.125
        return {width: width , height: height} 
      }
    componentDidMount(){
          let rederDataShow = [];
          let getData =  ""
          var data = window.localStorage.getItem('projectList')
          data = JSON.parse(data)
          if(Object.keys(this.props.posts).length === 0 && data){
                  getData = data
          }else{
              getData = this.props.posts.data
          
          }
          if(getData && Object.keys(getData).length !== 0){
              var rederData = getData.radar || {}
              for(let key in rederData){
                  let itemName = ""
                  let obj = {}
                  switch(key){
                    case 'prepayScore' : itemName = "预存电费" ;break;
                    case 'dlScore' : itemName = "用电电量"; break;
                    case 'payScore': itemName = "缴费方式" ;break;
                    case 'timelyScore' : itemName = "及时缴费";break;
                    default :  itemName = "" ;
                  }
                  obj.item = itemName;
                  obj.user = 'A';
                  obj.score = rederData[key]
                  rederDataShow.push(obj)
                  
              }
              this.setState({
                  star: getData.annualBill.xjTag
              })
          }
        this.radar(rederDataShow)
    }
    radar(data){
        var chart = new F2.Chart({
            id: 'rader',
            pixelRatio: window.devicePixelRatio
          });
          
          chart.coord('polar');
          chart.source(data, {
            score: {
                min: 0,
                max: 120,
                nice: false,
                tickCount: 4
              }
          });
          chart.legend(false)
          chart.axis('score', {
            label:null,
            grid: function grid(text) {
                if (text === '120') {
                  return {
                    lineDash: null
                  };
                }
              },
          });
        
          chart.axis('item', {
              label:function label(text) {
               
                var cfg = {};
                cfg.fill = '#000';
                return cfg;
            },
            grid: {
              lineDash: null
            }
          });
          chart.line().position('item*score').color('#FE5C5B');
          chart.point().position('item*score').color('#FE5C5B').style({
            stroke: '#fff',
            lineWidth: 1
          });
          chart.render();
    }
    render(){
        let {width ,height} =  this.getConverWidthAndHeight()
        let showStar = [];
        let starName = ""
        for(let i = 0 ; i< this.state.star ; i++){
            showStar.push(<img key={i}  src={starbg} />)
        }
        for(let i = 0; i< 5-this.state.star ; i++){
            showStar.push(<img key={i}  src={kongStar} />)
        }
        
        switch(this.state.star){
            case 1 : starName = "一" ; break;
            case 2 : starName = "二" ; break;
            case 3 : starName = "三" ; break;
            case 4 : starName = "四" ; break;
            case 5 : starName = "五" ; break;
            default : starName = ""
        }
        return (
          
            <div className="star"> 
                <Link to="/share"> 
                    <header>
                        <p>良好的用电行为可以提高您的星级。 高星级客户将享有积分倍享、电费 透支、专属客户经理等服务。</p> 
                    </header>
                    <section> 
                    <canvas id="rader" style={{width: width,height:height}}></canvas>
                    </section>
                    <footer>
                        <div className="showStar"> { showStar } <span>{starName}星级用户</span></div>
                    </footer>
                </Link>
            </div>
        ) 
    }
}
Star.PropTypes = {
    posts: PropTypes.array.isRequired
}
const mapStateToProps = state =>({
    posts: state.posts.items
})
export default connect(mapStateToProps,{})(Star)