import React,{Component} from 'react';
import{Link} from "react-router-dom";
import F2 from '@antv/f2';
import PieLabel  from '@antv/f2/lib/plugin/pie-label'
import './girlPay.less'
import PropTypes  from "prop-types";
import { connect } from "react-redux";
F2.Chart.plugins.register(PieLabel)
class GirlPay extends Component {
    constructor(props){
        super(props)
        this.state={
          payTimes: 0,
          tradePreference: "",
          elecInteg: 0
        }
    }
     // 用于计算cover的宽高
     getConverWidthAndHeight(){
        var docEl = document.documentElement
        let maxWidth = 750
        let cw = docEl.clientWidth>maxWidth ? maxWidth : docEl.clientWidth
        var fontSize =16 * (cw / 320);
        const width = fontSize *  15.375
        const height = fontSize * 10.5
        return {width: width , height: height} 
      }
    componentDidMount(){   
        var pieData = [];
        let getData =  ""
        let showPieData = {}
        var data = window.localStorage.getItem('projectList')
        data = JSON.parse(data)
        if(Object.keys(this.props.posts).length === 0 && data){
                getData = data
        }else{
            getData = this.props.posts.data
        
        }
        if(getData && Object.keys(getData).length !== 0){
          let i = 0
          this.setState({
            payTimes: getData.annualBill.payTimes || 0,
            tradePreference: getData.annualBill.tradePreference || "",
            elecInteg: getData.annualBill.elecInteg || 0
          })
          showPieData = getData.paymentTimes
          for(let key in showPieData){
            var name = ""
            var obj = {
            }
            switch(key){
              case "styytTimes" : name = "实体营业厅" ;break;
              case "wsyytTmes" : name = "网上营业厅" ;break;
              case "zfbTimes": name="支付宝" ;break;
              case "wxTimes": name="微信" ; break;
              case "yhdsTimes": name="银行代收" ;break;
              case "elseTimes": name="其他社会化代收"; break;
              default: name= "";
            }
            obj.y = showPieData[key]
            obj.name = name
            obj.const =  'const'
            pieData.push(obj)
            i++
          }
        }
        this.showPie(pieData)
    }
    showPie(data){
      var chart = new F2.Chart({
        id: 'pieLabel',
        pixelRatio: window.devicePixelRatio
      });
      
      chart.source(data);
      chart.coord('polar', {
        transposed: true,
        radius: 0.75
      });
      chart.legend(false);
      chart.axis(false);
      chart.tooltip(false);
      
      // 添加饼图文本
      chart.pieLabel({
        sidePadding: 40,
        label1: function label1(data, color) {
          return {
            text: data.name,
            fill: color
          };
        },
        label2: function label2(data) {
          return {
            text: '￥' + String(Math.floor(data.y * 100) / 100).replace(/\B(?=(\d{3})+(?!\d))/g, ','),
            fill: '#808080',
            fontWeight: 'bold'
          };
        }
      });
      
      chart.interval().position('const*y').color('name', ['#1890FF', '#13C2C2', '#2FC25B', '#FACC14', '#F04864']).adjust('stack');
      chart.render();
    }
    render(){
        var {width ,height} =  this.getConverWidthAndHeight()
        return (
            <div className="girlPay">  
                <Link to="/star"> 
                    <header>
                       <h1>2018年</h1> 
                       <p>共缴电费 <span style={{ color: 'red'}}>{this.state.payTimes} </span>笔 </p>
                       <p>习惯 <span style={{ color: 'red'}}>{ this.state.tradePreference }</span></p>
                    </header>
                    <section> 
                        <canvas id="pieLabel" style={{width: width,height:height}}></canvas>
                    </section>
                    <footer>
                        <p>电力积分  <span style={{ color: 'red'}}>{ this.state.elecInteg}</span></p>
                    </footer>
                </Link>
            </div>
        )
    }
}
GirlPay.PropTypes = {
  posts: PropTypes.array.isRequired
}
const mapStateToProps = state =>({
  posts: state.posts.items
})
export default connect(mapStateToProps,{})(GirlPay)