import  { OnClick } from './types'

export const onclick = (consno,orgno,channel ) => dispatch =>{
    let obj =  { consno,orgno,channel}
    fetch('/bill/QueryBill?consno='+ consno +'&orgno='+ orgno +'&channel='+ channel,{
        method: "get",
        headers: {
            'Content-Type': 'application/json'
        }
    })
    .then(res => res.json())
    .then(data => dispatch({
        type: OnClick,
        Payload: data.data
    }))
}

