import React,{ Component} from 'react'
import shareMa from '../images/shareMa.png'
import './share.less'
import PropTypes  from "prop-types";
import { connect } from "react-redux";

class Share extends Component{
    constructor(props){
        super(props)
        this.state={
            managerName: "",
            managerTel:"",
            yearlySing:"新年快乐",
            yearName: "",
        }
    }
    luckName(){
        var name = this.state.yearName
        this.setState({
            yearlySing:name || ""
        })
    }
    componentDidMount(){
        let getData =  ""
        var data = window.localStorage.getItem('projectList')
        data = JSON.parse(data)
        if(Object.keys(this.props.posts).length === 0 && data){
                getData = data
        }else{
            getData = this.props.posts.data
        
        }
        if(getData && Object.keys(getData).length !== 0){
          this.setState({
            managerName: getData.annualBill.managerName || "",
            managerTel: getData.annualBill.managerTel || "",
            yearName:  getData.annualBill.yearlySing || ""
          })
        }
    }
    render(){
        return (
            <div className="share"> 
                <header>
                    <p>您的专属客户经理：<span style={{ color:'red' }}>{this.state.managerName} 手机号：{this.state.managerTel}</span></p> 
                </header>
                <section>
                    <p className="newYear">{this.state.yearlySing}</p>
                </section>
                <section> 
                    <img src={shareMa} />
                    <ul>
                        <li>电e宝</li>
                        <li>掌上电力</li>
                        <li>电力微信公众号 </li>
                    </ul>
                </section>
                <footer>
                    <button onClick={this.luckName.bind(this)}>点击抽取新年幸运签 </button>
                    <div className="shareGo"></div>
                </footer>
            </div>
        ) 
    }
}
Share.PropTypes = {
    posts: PropTypes.array.isRequired
}
const mapStateToProps = state =>({
    posts: state.posts.items
})
export default connect(mapStateToProps,{})(Share)