import React, { Component } from 'react';
import { BrowserRouter, Route,Redirect } from "react-router-dom";
import { store } from "./store.js";
import { Provider } from "react-redux";
import Welcome from './components/welcome/welcome';
import ShowAll from './components/showAll/showAll';
import AppIndex from "./components/appIndex/appIndex"
import YearDetail from "./components/yearDetail/yearDetail"
import YearHot from "./components/yearHot/yearHot"
import Ladder from "./components/ladder/ladder"
import GirlPay from './components/girlPay/girlPay';
import Star from './components/star/star';
import Share from './components/share/share';
// 路由的配置。
class Root extends Component {
  render() {
    return (
      <Provider store = {store}>
        <BrowserRouter>
          <div className="root">
            <Route path="/"   exact render={()=>(
                <Redirect to='/appIndex' />
            )}/>
            <Route path="/appIndex" component={AppIndex}></Route>
            <Route path="/Welcome" component={Welcome}></Route>
            <Route path="/showAll" component={ShowAll}></Route>
            <Route path="/yearDetail" component={YearDetail}></Route>
            <Route path="/yearHot" component={YearHot}></Route>
            <Route path="/ladder" component={Ladder }></Route>
            <Route path="/girlPay" component={GirlPay }></Route>
            <Route path="/star" component={Star }></Route>
            <Route path="/share" component={Share }></Route>
          </div>
        </BrowserRouter>
      </Provider>
   )}
}

export default Root;
